<template>
    <div style="width: 500px;">
        <el-input
                placeholder="输入部门进行搜索..."
                prefix-icon="el-icon-search"
                v-model="filterText">
        </el-input>
        <el-tree
                :data="deps"
                :props="defaultProps"
                :filter-node-method="filterNode"
                :expand-on-click-node="false"
                ref="tree">
            <span class="custom-tree-node" style="display: flex;justify-content:space-between;width:100%;"
                  slot-scope="{ node, data }">
        <span>{{ data.name }}</span>
        <span>
          <el-button
                  type="primary"
                  size="mini"
                  class="depBen"
                  @click="() => showDep(data)">
                    添加部门
          </el-button>
          <el-button
                  type="danger"
                  size="mini"
                  class="depBen"
                  @click="() => delDep(data)">
            删除部门
          </el-button>
        </span>
      </span>
        </el-tree>
        <el-dialog
                title="添加部门"
                :visible.sync="dialogVisible"
                width="30%">
            <div>
                <table>
                    <tr>
                        <td>
                            <el-tag>上级部门</el-tag>
                        </td>
                        <td><span>{{pName}}</span></td>
                    </tr>
                    <tr>
                        <td>
                            <el-tag>部门名称</el-tag>
                        </td>
                        <td>
                            <el-input v-model="dep.name" placeholder="请输入部门名称..."></el-input>
                        </td>
                    </tr>
                </table>

            </div>
            <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="initAddDep">确 定</el-button>
  </span>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        name: "DepMana",
        data() {
            return {
                filterText: '',
                deps: [],
                dialogVisible: false,
                defaultProps: {
                    children: 'children',
                    label: 'name'
                },
                dep: {
                    name: '',
                    parentid: -1
                },
                pName: ''
            }
        },
        watch: {
            filterText(val) {
                this.$refs.tree.filter(val);
            }
        },
        mounted() {
            this.initDepts();
        },
        methods: {
            add2Deps(dep, obj) {
                for (let i = 0; i < dep.length; i++) {
                    let d = dep[i];
                    if (d.id == obj.parentid) {
                        d.children = d.children.concat(obj);
                        if(d.children.length>0){
                            d.parent=true;
                        }
                        return;
                    } else {
                        this.add2Deps(d.children, obj);
                    }
                }
            },
            initDepClean() {
                this.dep = {
                    name: '',
                    parentid: -1
                };
                this.pName = '';
            },
            initAddDep() {
                this.postRequest("/system/basic/dept/", this.dep).then(resp => {
                    if (resp) {
                        this.dialogVisible = false;
                        this.add2Deps(this.deps, resp.obj);
                        this.initDepClean();
                    }
                })
            },
            delete2Dep(p, deps, id) {
                for (let i = 0; i < deps.length; i++) {
                    let d = deps[i];
                    if (d.id == id) {
                        deps.splice(i, 1);
                        if (deps.length == 0) {
                            p.parent = false;
                        }
                        return;
                    } else {
                        this.delete2Dep(d, d.children, id);
                    }
                }
            },
            delDep(data) {
                if (data.parent) {
                    this.$message.error(data.name + "部门，无法删除！");
                } else {
                    this.$confirm('此操作将永久删除【' + data.name + '】个数据, 是否继续?', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        this.deleteRequest("/system/basic/dept/" + data.id).then(resp => {
                            if (resp) {
                                this.delete2Dep(null, this.deps, data.id);
                                // this.dialogVisible = false;
                            }
                        })
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: '已取消删除'
                        });
                    });
                }

            },
            showDep(data) {
                this.pName = data.name;
                this.dep.parentid = data.id;
                this.dialogVisible = true;
            },
            initDepts() {
                this.getRequest("/system/basic/dept/").then(resp => {
                    if (resp) {
                        this.deps = resp;
                    }
                })
            },
            filterNode(value, data) {
                if (!value) return true;
                return data.name.indexOf(value) !== -1;
            }
        }
    }
</script>

<style>
    .depBen {
        padding: 2px;
    }

</style>