<template>
    <div>
        <div class="permissManaTools">
            <el-input size="small" placeholder="请输入角色英文名" v-model="role.name">
                <template slot="prepend">ROLE_</template>
            </el-input>
            <el-input size="small" placeholder="请输入角色中文名" v-model="role.namezh"></el-input>
            <el-button size="small" type="primary" icon="el-icon-plus" @click="addRole">添加角色</el-button>
        </div>
        <div style="margin-top: 10px;width: 650px;">
            <el-collapse v-model="activeNames" accordion @change="change">
                <el-collapse-item :title="r.namezh" :name="r.id" v-for="(r,index) in roles" :key="index">
                    <el-card class="box-card">
                        <div slot="header" class="clearfix">
                            <span>可访问资源</span>
                            <el-button style="float: right; padding: 3px 0;color: #ff0000" icon="el-icon-delete" type="text" @click="deleteRole(r)"></el-button>
                        </div>
                        <div>
                            <el-tree :data="menusData"
                                     show-checkbox
                                     ref="tree"
                                     :key="index"
                                     node-key="id"
                                     :default-checked-keys="menusId"
                                     :props="defaultProps">
                            </el-tree>
                            <div style="justify-content: flex-end;display: flex;">
                                <el-button @click="cancleUpdate">取消修改</el-button>
                                <el-button type="primary" @click="doUpdate(r.id,index)">确认修改</el-button>
                            </div>
                        </div>
                    </el-card>
                </el-collapse-item>
            </el-collapse>
        </div>
    </div>
</template>

<script>
    export default {
        name: "PermissMana",
        data(){
            return {
                activeNames:-1,
                role:{
                    name:'',
                    namezh:''
                },
                roles:[],
                defaultProps: {
                    children: 'children',
                    label: 'name'
                },
                menusData:[],
                menusId:[]
            }
        },
        mounted(){
          this.initRoles()
        },
        methods:{
            deleteRole(r){
                this.$confirm('此操作将永久删除【' + r.namezh + '】个数据, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteRequest("/system/basic/permiss/role/"+r.id).then(resp=>{
                        if(resp){
                            this.initRoles();
                            this.activeNames=-1;
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            addRole(){
                if(this.role.name && this.role.namezh){
                    this.postRequest("/system/basic/permiss/",this.role).then(resp=>{
                        if(resp){
                            this.initRoles();
                            this.role.name='';
                            this.role.namezh='';
                        }
                    })
                }else{
                    this.$message.error("请输入角色信息！");
                }
            },
            cancleUpdate(){
                this.activeNames=-1;
            },
            doUpdate(rid,index){
                let treeIndex=this.$refs.tree[index];
                let selectKeys=treeIndex.getCheckedKeys(true);
                let url = '/system/basic/permiss/?rid='+rid;
                selectKeys.forEach(key=>{
                    url+='&mids='+key;
                });
                this.putRequest(url).then(resp=>{
                    if(resp){
                        // this.initRoles();
                        this.activeNames=-1;
                    }
                })
            },
            change(rid){
                if(rid){
                    this.initMenus();
                    this.initSelectMenus(rid);
                }
            },
            initSelectMenus(rid){
                this.getRequest("system/basic/permiss/mids/"+rid).then(resp=>{
                    if(resp){
                        this.menusId=resp;
                    }
                })
            },
            initMenus(){
              this.getRequest("/system/basic/permiss/menus").then(resp=>{
                  if(resp){
                      this.menusData=resp;
                  }
              })
            },
            initRoles(){
                this.getRequest("/system/basic/permiss/").then(resp=>{
                    if(resp){
                        this.roles=resp;
                    }
                })
            }
        }
    }
</script>

<style>
    .permissManaTools{
        display: flex;
        justify-content: flex-start;
    }
    .permissManaTools .el-input{
        width:300px;
        margin-right: 6px;
    }

</style>