<template>
    <div>
        <div style="margin-top:10px;display: flex;justify-content: center;">
            <el-input v-model="keywords" placeholder="通过用户名搜索..." prefix-icon="el-icon-search"
                      style="width:400px;margin-right:10px;" @keydown.enter.native="doSearch"></el-input>
            <el-button icon="el-icon-search" type="primary" @click="doSearch">搜索</el-button>

        </div>
        <div class="hr-container">
            <el-card class="hr-card" v-for="(hr,index) in hrs" :key="index">
                <div slot="header" class="clearfix">
                    <span>{{hr.name}}</span>
                    <el-button style="float: right; padding: 3px 0;color:#ff0000" type="text"></el-button>
                </div>
                <div>
                    <div class="container-img">
                        <img :src="hr.userface" :alt="hr.name" :title="hr.name" class="userface-img">
                    </div>
                    <div class="container-content">
                        <div>用户名：{{hr.name}}</div>
                        <div>手机号码：{{hr.name}}</div>
                        <div>电话号码：{{hr.name}}</div>
                        <div>地址：{{hr.name}}</div>
                        <div>用户状态：
                            <el-switch
                                    v-model="hr.enabled"
                                    active-color="#13ce66"
                                    inactive-color="#ff4949"
                                    @change="enableUpdate(hr)"
                                    active-text="开启"
                                    inactive-text="禁用">
                            </el-switch>
                        </div>
                        <div>用户角色：
                            <el-tag type="success" style="margin-left: 10px;" v-for="(role,indexL) in hr.roles"
                                    :key="indexL">{{role.namezh}}
                            </el-tag>
                            <el-popover
                                    placement="right"
                                    title="角色列表"
                                    width="200"
                                    @show="showHr(hr)"
                                    @hide="hideHr(hr)"
                                    trigger="click">
                                <el-select v-model="selectRoles" multiple placeholder="请选择">
                                    <el-option
                                            v-for="(r,indexR) in allRoles"
                                            :key="indexR"
                                            :label="r.namezh"
                                            :value="r.id">
                                    </el-option>
                                </el-select>
                                <el-button slot="reference" type="text" icon="el-icon-more"
                                           style="margin-left: 5px;"></el-button>
                            </el-popover>
                        </div>
                        <div>备注：</div>
                    </div>
                </div>
            </el-card>
        </div>
    </div>
</template>

<script>
    export default {
        name: "SysHr",
        data() {
            return {
                keywords: '',
                hrs: [],
                allRoles: [],
                selectRoles: []
            }
        },
        mounted() {
            this.initHrs();

        },
        methods: {
            doSearch(){
                this.initHrs();
            },
            hideHr(hr) {
                let roles = [];
                Object.assign(roles, hr.roles);
                let flag = false;
                if (roles.length != this.selectRoles.length) {
                    flag = true;
                } else {
                    for (let i = 0; i < roles.length; i++) {
                        let ro = roles[i];
                        for (let j = 0; j < this.selectRoles.length; j++) {
                            let sr = this.selectRoles[j];
                            if (ro.id == sr) {
                                roles.splice(i, 1);
                                i--;
                                break;
                            }
                        }
                    }
                    if (roles.length != 0) {
                        flag = true;
                    }
                }
                if (flag) {
                    let url = '/system/hr/role?hrid=' + hr.id;
                    this.selectRoles.forEach(rp => {
                        url += '&rids=' + rp;
                    });
                    this.putRequest(url).then(resp => {
                        if (resp) {
                            this.initHrs();
                        }
                    })
                }

            },
            showHr(hr) {
                this.initRoles();
                let role = hr.roles;
                this.selectRoles = [];
                role.forEach(ro => {
                    this.selectRoles.push(ro.id);
                });

            },
            initRoles() {
                this.getRequest("/system/hr/roles/").then(resp => {
                    if (resp) {
                        this.allRoles = resp;
                    }
                })
            },
            enableUpdate(hr) {
                this.putRequest("/system/hr/", hr).then(resp => {
                    if (resp) {
                        this.initHrs();
                    }
                })
            },
            initHrs() {
                this.getRequest("/system/hr/?keywords="+this.keywords).then(resp => {
                    if (resp) {
                        this.hrs = resp;
                    }
                })
            }
        }
    }
</script>

<style>
    .container-content {
        font-size: 12px;
        color: #409eff;
    }

    .container-img {
        width: 100%;
        display: flex;
        justify-content: center;
    }

    .userface-img {
        width: 72px;
        height: 72px;
        border-radius: 72px;
    }

    .hr-container {
        margin-top: 20px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
    }

    .hr-card {
        width: 350px;
        margin-bottom: 20px;
    }

</style>