<template>
    <div>
        <div>
            <el-input size="small" @keydown.enter.native="addJobLevel" v-model="jl.name" style="width: 200px;" prefix-icon="el-icon-plus" place-holder="添加职称..."></el-input>
            <el-select v-model="jl.titlelevel" placeholder="职位级别" size="small" style="margin-left:5px;margin-right: 5px;">
                <el-option
                        v-for="item in titlelevels"
                        :key="item"
                        :label="item"
                        :value="item">
                </el-option>
            </el-select>
            <el-button size="small" @click="addJobLevel" icon="el-icon-plus" type="primary"></el-button>
        </div>
        <div style="margin-top: 10px;">
            <el-table
                    :data="jls"
                    border
                    size="small"
                    @selection-change="handleSelectionChange"
                    style="width: 100%;">
                <el-table-column
                        type="selection"
                        width="55">
                </el-table-column>
                <el-table-column
                        prop="id"
                        label="编号"
                        width="55">
                </el-table-column>
                <el-table-column
                        prop="name"
                        label="职称名称"
                        width="150">
                </el-table-column>
                <el-table-column
                        prop="titlelevel"
                        width="150"
                        label="职称级别">
                </el-table-column>
                <el-table-column
                        prop="createdate"
                        width="190"
                        label="创建时间">
                </el-table-column>
                <el-table-column
                        width="90"
                        label="是否启用">
                    <template slot-scope="scope">
                        <el-tag type="success" v-if="scope.row.enabled">已启用</el-tag>
                        <el-tag type="danger" v-else>未启用</el-tag>
                    </template>
                </el-table-column>
                <el-table-column
                        prop="enabled"
                        label="操作">
                    <template slot-scope="scope">
                        <el-button
                                size="mini"
                                @click="showEdit(scope.$index, scope.row)">编辑
                        </el-button>
                        <el-button
                                size="mini"
                                type="danger"
                                @click="handleDelete(scope.$index, scope.row)">删除
                        </el-button>
                    </template>

                </el-table-column>
            </el-table>
            <el-button @click="delAll" type="danger" size="small" style="margin-top:8px;"
                       :disabled="jlsList.length==0">批量删除
            </el-button>
        </div>
        <el-dialog title="修改职位" :visible.sync="dialogVisible" width="30%">
            <div>
                <el-tag>职位名称</el-tag>
                <el-input class="empty" size="small" style="width:48%;" v-model="jlcopy.name"></el-input>
                <br>
                <el-tag>职位级别</el-tag>
                <el-select class="empty" v-model="jlcopy.titlelevel" placeholder="职位级别" size="small" style="margin-left:5px;margin-right: 5px;">
                    <el-option
                            v-for="item in titlelevels"
                            :key="item"
                            :label="item"
                            :value="item">
                    </el-option>
                </el-select>
                <br>
                <el-tag>是否启用</el-tag>
                <el-switch
                        v-model="jlcopy.enabled"
                        class="empty"
                        active-color="#13ce66"
                        inactive-color="#ff4949"
                        active-text="启用"
                        inactive-text="禁用">
                </el-switch>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button size="small" @click="dialogVisible = false">取 消</el-button>
                <el-button size="small" type="primary" @click="doUpdate">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        name: "JobLevelMana",
        data(){
            return {
                jl:{
                    name:'',
                    titlelevel:''
                },
                titlelevels:[
                    '正高级',
                    '副高级',
                    '中级',
                    '初级',
                    '员级'
                ],
                jlcopy:{
                  name:'',
                  titlelevel:'',
                  enabled:false
                },
                jls:[],
                jlsList:[],
                dialogVisible:false
            }
        },
        mounted(){
            this.initjls();
        },
        methods:{
            delAll(){
                this.$confirm('此操作将永久删除【' + this.jlsList.length + '】个数据, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    let ids = '?';
                    this.jlsList.forEach(item => {
                        ids += 'ids=' + item.id + '&';
                    });
                    // console.log(ids);
                    this.deleteRequest("/system/basic/joblevel/" + ids).then(resp => {
                        if (resp) {
                            this.initjls();
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            doUpdate(){
              this.putRequest("/system/basic/joblevel/",this.jlcopy).then(resp=>{
                  if(resp){
                      this.initjls();
                      this.dialogVisible=false;
                  }
              })
            },
            initjls(){
                this.getRequest("/system/basic/joblevel/").then(resp=>{
                    if(resp){
                        this.jls=resp;
                    }
                })
            },
            addJobLevel(){
                if(this.jl.name && this.jl.titlelevel){
                    this.postRequest("/system/basic/joblevel/",this.jl).then(resp=>{
                        if(resp){
                            this.initjls();
                            this.jl.name='';
                            this.jl.titlelevel='';
                        }
                    })
                }else{
                    this.$message.error("添加字段不能为空！")
                }
            },
            handleDelete(index,data){
                this.$confirm('此操作将永久删除【' + data.name + '】职位, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteRequest("/system/basic/joblevel/" + data.id).then(resp => {
                        if (resp) {
                            this.initjls();
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            showEdit(index,data){
                Object.assign(this.jlcopy, data);
                this.dialogVisible=true;
            },
            handleSelectionChange(val){
                this.jlsList=val;

            }
        }
    }
</script>

<style>
    .empty{
        margin-left:5px;
        margin-right: 5px;
    }

</style>